<template>
  <div>
    <div class="jumbotron" v-if="job">
      <h1 class="card-header">Job ID {{job.DAG}}</h1>
      <div class="card-body">
        <h5 class="card-title">{{this.$data.job.Total}} Total Jobs</h5>
        <h6 class="card-subtitle text-muted">Started at {{job.Time}} on {{job.Date}}</h6>
      </div>
      <div class="card-footer text-muted">
        <div class="progress">
          <div
            class="progress-bar"
            role="progressbar"
            :style="job.percent"
            aria-valuenow="5"
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
        </div>
          <h1>{{job.percent}}% Completed</h1>
      </div>
      <b-button v-b-modal.modal-1 class="btn btn-primary space-button btn-lg" @click="confirm=true">Cancel DAG</b-button>
      <b-modal v-if="confirm" id="modal-1" :title="'Cancelling DAG ' + job.DAG">
        <form>
          <div class="form-group">
            <label for="dagID">Type the DAG ID to confirm</label>
            <input v-model="dagID" class="form-control" id="dagID" placeholder="DAG ID">
          </div>
        </form>
        <div slot="modal-footer" class="w-100">
          <b-button variant="primary" size="md" class="float-right" @click="confirm=false">Cancel</b-button>
          <b-button
            variant="primary"
            size="md"
            class="float-right"
            style="margin-right: 2%"
            @click="confirmDelete"
          >Submit</b-button>
        </div>
      </b-modal>
    </div>
  </div>
</template>

<script>
const API_URL = "http://localhost:8000/api";
export default {
  name: "JobPage",
  data: () => ({
    job: {},
    confirm: false,
    dagID: null
  }),
  mounted() {
    this.load();
  },
  methods: {
    async load() {
      let response = await fetch(API_URL);
      if (response.status === 200) {
        const data = await response.json();
        for (let i = 0; i < data.jobs.length; i++) {
          if (data.jobs[i].DAG.replace("DAG+", "") === this.$route.params.id) {
            this.job = {
              ...data.jobs[i],
              DAG: data.jobs[i].DAG.replace("dag+", ""),
              percent:
                "width: " +
                (100 * data.jobs[i].Done) / data.jobs[i].Total +
                "%;"
            };
          }
        }
      }
    },
    async sendDelete() {
      let response = await fetch(API_URL, {
        method: "POST",
        body: JSON.stringify({ id: this.dagID })
      });
      if (response.status === 200) {
        this.$router.push('/');
      }
    },
    toggle() {
      this.confirm = !this.confirm;
    },
    confirmDelete() {
      if (this.dagID === this.job.DAG) {
        // eslint-disable-next-line no-console
        console.log("yes");
        this.sendDelete()
      } else {
        // eslint-disable-next-line no-console
        console.log("no");
      }
    }
  }
};
</script>

<style>
.space-button {
  margin: 5%;
}
</style>
